export default function Collection() {
  const [category, setCategory] = useState("");
  const [size, setSize] = useState("");
  const [color, setColor] = useState("");
  const [rating, setRating] = useState(0);
  const [maxPrice, setMaxPrice] = useState(150);

  const filtered = useMemo(() => {
    return products.filter((p) => {
      return (
        (!category || p.category === category) &&
        (!size || p.size === size) &&
        (!color || p.color === color) &&
        (!rating || p.rating >= parseInt(rating)) &&
        p.price <= maxPrice
      );
    });
  }, [category, size, color, rating, maxPrice]);

  const resetFilters = () => {
    setCategory("");
    setSize("");
    setColor("");
    setRating(0);
    setMaxPrice(150);
  };

  return (
    <div className="flex flex-col lg:flex-row min-h-screen px-4 sm:px-6 py-10 gap-10">
      {/* الشريط الجانبي للفلاتر */}
       <div className="lg:hidden">
        <Sheet>
          <SheetTrigger asChild>
            <Button className="w-full mb-4">فتح الفلاتر</Button>
          </SheetTrigger>
          <SheetContent side="left" className="w-3/4 p-4">
            <FillterCollection
              setCategory={setCategory}
              setSize={setSize}
              setColor={setColor}
              setRating={setRating}
              maxPrice={maxPrice}
              setMaxPrice={setMaxPrice}
              resetFilters={resetFilters}
            />
          </SheetContent>
        </Sheet>
      </div>

      {/* شبكة المنتجات */}
      <main className="w-full lg:w-3/4">
        <h1 className="text-3xl font-bold mb-6">مجموعة الرجال</h1>

        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 xl:grid-cols-4 gap-6">
          {filtered.map((product) => (
            <Card key={product.name} className="rounded-2xl shadow-md">
              <CardContent className="p-4">
                <img src={product.image} alt={product.name} className="w-full h-48 object-cover mb-4 rounded-lg" />
                <h3 className="text-lg font-semibold">{product.name}</h3>
                <p className="text-sm text-muted-foreground">${product.price}</p>
                <p className="text-xs text-yellow-500">التقييم: {product.rating}★</p>
              </CardContent>
            </Card>
          ))}
        </div>
      </main>
    </div>
  );
}
